import { DataSourceJsonData } from '@grafana/data';
import { DataQuery, DataSourceRef } from '@grafana/schema';

import { Api } from '../api';

/**
 * Data Source test status
 */
export enum DataSourceTestStatus {
  SUCCESS = 'success',
  ERROR = 'error',
}

/**
 * Query
 */
export interface Query extends DataQuery {}

/**
 * Data Source Options
 */
export interface DataSourceOptions extends DataSourceJsonData {
  /**
   * URL
   *
   * @type {string}
   */
  url?: string;
}

/**
 * Secure JSON Data
 */
export interface SecureJsonData {
  /**
   * Token
   *
   * @type {string}
   */
  token?: string;
}

/**
 * Error Code
 */
export enum ErrorCode {
  EMPTY_TOKEN = 'emptyToken',
  INVALID_TOKEN = 'invalidToken',
  LICENSE_VERIFICATION_FAILED = 'licenseVerificationFailed',
  LIMIT_REACHED = 'limitReached',
}

/**
 * Error Response
 */
export interface ErrorResponse {
  /**
   * Code
   *
   * @type {ErrorCode}
   */
  code: ErrorCode;

  /**
   * Message
   *
   * @type {string}
   */
  message: string;
}

/**
 * Api Name
 */
export enum ApiName {
  CREATE_ALERT = 'createAlert',
  CREATE_WEBHOOK = 'createWebhook',
  DELETE_ALERT = 'deleteAlert',
  DELETE_WEBHOOK = 'deleteWebhook',
  GET_ALERTS = 'getAlerts',
  GET_TRIGGER_EVENT = 'getTriggerEvent',
  GET_WEBHOOKS = 'getWebhooks',
  GET_DEFAULT_WEBHOOK = 'getDefaultWebhook',
  PING = 'ping',
  UPDATE_ALERT = 'updateAlert',
  UPDATE_WEBHOOK = 'updateWebhook',
  TEST_WEBHOOK = 'testWebhook',
  UPDATE_ALERT_STATUS = 'updateAlertStatus',
}

/**
 * Data Source Ref for API
 */
export interface DataSourceApi extends DataSourceRef {
  /**
   * Api
   */
  api?: Api;
}
