import { DataSourceApi, Webhook, WebhookMethod, WebhookType } from '@bi/volkovlabs-engine-datasource';
import { Alert, Button, FieldSet, InlineField, Input, RadioButtonGroup, useStyles2 } from '@grafana/ui';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { getStyles } from './WebhookDetails.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * On Save
   */
  onSave: (data: Webhook) => void;

  /**
   * Initial
   */
  initial?: Webhook;
}

/**
 * Webhook Details
 */
export const WebhookDetails: React.FC<Props> = ({ datasource, onSave, onClose, initial }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Alert
   */
  const [webhook, setWebhook] = useState<Webhook>(
    initial || {
      id: '',
      title: '',
      type: WebhookType.REQUEST,
      options: {
        url: '',
      },
      isDefault: false,
    }
  );

  /**
   * Testing Results
   */
  const [isTesting, setTesting] = useState(false);
  const [testResult, setTestResult] = useState<{ ok: boolean; message: string } | null>(null);

  /**
   * Saving
   */
  const [saving, setSaving] = useState(false);

  /**
   * On Test
   */
  const onTest = useCallback(async () => {
    if (!datasource || !datasource.api) {
      return;
    }

    /**
     * Reset testing state
     */
    setTesting(true);
    setTestResult(null);

    const { useFor, useForByDefault, ...webhookForTesting } = webhook;

    /**
     * Test Result
     */
    const data = await datasource.api.webhooks.test(webhookForTesting);

    /**
     * Save Test Results
     */
    setTestResult(data);
    setTesting(false);
  }, [datasource, webhook]);

  /**
   * Type Options
   */
  const typeOptions = useMemo(() => {
    return [
      {
        value: WebhookType.REQUEST,
        label: t('alertingPanel.panel.webhookType.request'),
        icon: 'cloud-upload',
      },
      {
        value: WebhookType.CUSTOM,
        label: t('alertingPanel.panel.webhookType.custom'),
        icon: 'bolt',
      },
    ];
  }, [t]);

  /**
   * Is Default Options
   */
  const isDefaultOptions = useMemo(() => {
    return [
      {
        value: true,
        label: t('alertingPanel.panel.webhookDetails.isDefaultOptions.enabled'),
        icon: 'check-square',
      },
      {
        value: false,
        label: t('alertingPanel.panel.webhookDetails.isDefaultOptions.disabled'),
        icon: 'square-shape',
      },
    ];
  }, [t]);

  /**
   * Method Options
   */
  const methodOptions = [
    {
      value: WebhookMethod.POST,
      label: WebhookMethod.POST,
    },
  ];

  /**
   * Is Invalid
   */
  const isInvalid = useMemo(() => {
    const isBaseInvalid = !webhook.title || !webhook.type;

    if (isBaseInvalid) {
      return true;
    }

    if (webhook.type === WebhookType.REQUEST) {
      return !webhook.options.url;
    }

    return false;
  }, [webhook]);

  return (
    <>
      <FieldSet>
        <InlineField
          label={t('alertingPanel.panel.webhookDetails.titleLabel')}
          grow={true}
          labelWidth={14}
          required={true}
        >
          <Input
            onChange={(event) => {
              setWebhook({
                ...webhook,
                title: event.currentTarget.value,
              });
            }}
            value={webhook.title}
          />
        </InlineField>
        <InlineField
          label={t('alertingPanel.panel.webhookDetails.typeLabel')}
          grow={true}
          labelWidth={14}
          required={true}
        >
          <RadioButtonGroup
            onChange={(type) => {
              setWebhook((webhook) => {
                if (type === WebhookType.REQUEST) {
                  return {
                    ...webhook,
                    type,
                    options: {
                      url: '',
                    },
                  };
                }

                if (type === WebhookType.CUSTOM) {
                  return {
                    ...webhook,
                    type,
                    options: {},
                  };
                }

                return webhook;
              });
            }}
            value={webhook.type}
            options={typeOptions}
          />
        </InlineField>
        <InlineField
          label={t('alertingPanel.panel.webhookDetails.defaultLabel')}
          grow={true}
          labelWidth={14}
          disabled={initial?.isDefault && !!initial?.useForByDefault?.length}
        >
          <RadioButtonGroup
            onChange={(isDefault) => {
              setWebhook({
                ...webhook,
                isDefault,
              });
            }}
            value={webhook.isDefault}
            options={isDefaultOptions}
          />
        </InlineField>
      </FieldSet>

      {webhook.type === WebhookType.REQUEST && (
        <FieldSet label={t('alertingPanel.panel.webhookDetails.requestOptions.label')}>
          <InlineField
            label={t('alertingPanel.panel.webhookDetails.requestOptions.urlLabel')}
            grow={true}
            labelWidth={14}
            required={true}
          >
            <Input
              onChange={(event) => {
                setWebhook({
                  ...webhook,
                  options: {
                    ...webhook.options,
                    url: event.currentTarget.value,
                  },
                } as typeof webhook);
              }}
              value={webhook.options.url}
            />
          </InlineField>
          <InlineField
            label={t('alertingPanel.panel.webhookDetails.requestOptions.methodLabel')}
            grow={true}
            labelWidth={14}
          >
            <RadioButtonGroup value={WebhookMethod.POST} options={methodOptions} />
          </InlineField>
        </FieldSet>
      )}
      {!!testResult && (
        <>
          {testResult.ok ? (
            <Alert severity="success" title={t('alertingPanel.panel.webhookDetails.testSuccessTitle')}>
              {t('alertingPanel.panel.webhookDetails.testSuccessMessage')}
            </Alert>
          ) : (
            <Alert severity="error" title={t('alertingPanel.panel.webhookDetails.testErrorTitle')}>
              {testResult.message}
            </Alert>
          )}
        </>
      )}
      {isTesting && (
        <Alert severity="info" title={t('alertingPanel.panel.webhookDetails.testProgressTitle')}>
          {t('alertingPanel.panel.webhookDetails.testProgressMessage')}
        </Alert>
      )}
      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose}>
          {t('alertingPanel.panel.webhookDetails.closeButton')}
        </Button>
        <Button onClick={onTest} disabled={isInvalid || isTesting} icon="heart-rate" variant="destructive">
          {t('alertingPanel.panel.webhookDetails.testButton')}
        </Button>
        <Button
          onClick={async () => {
            setSaving(true);
            await onSave(webhook);
            setSaving(false);
          }}
          disabled={isInvalid || saving}
        >
          {t('alertingPanel.panel.webhookDetails.saveButton')}
        </Button>
      </footer>
    </>
  );
};
