import { WebhookIcon } from '@bi/icons';
import {
  DataSourceApi,
  notifyError,
  notifySuccess,
  useData,
  Webhook,
  WebhookType,
} from '@bi/volkovlabs-engine-datasource';
import { EventBus } from '@grafana/data';
import { RefreshEvent } from '@grafana/runtime';
import {
  Alert,
  Button,
  Card,
  ConfirmModal,
  Drawer,
  IconButton,
  LoadingPlaceholder,
  TagList,
  useStyles2,
  useTheme2,
} from '@grafana/ui';
import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';
import { usePermissions } from '../../hooks';
import { WebhookDetails } from '../WebhookDetails';
import { getStyles } from './WebhooksList.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi}
   */
  datasource: DataSourceApi | null;

  /**
   * Event Bus
   *
   * @type {EventBus}
   */
  eventBus: EventBus;
}

/**
 * Webhooks List
 */
export const WebhooksList: React.FC<Props> = ({ datasource, eventBus }) => {
  /**
   * Theme and Styles
   */
  const theme = useTheme2();
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Permissions
   */
  const permissions = usePermissions();

  /**
   * States
   */
  const [isAdding, setAdding] = useState(false);
  const [editWebhook, setEditWebhook] = useState<Webhook | null>(null);
  const [deleteWebhook, setDeleteWebhook] = useState<Webhook | null>(null);
  const [deleting, setDeleting] = useState(false);

  /**
   * Webhooks
   */
  const {
    data: webhooks,
    loading,
    update: updateWebhooks,
    error,
  } = useData({
    datasource,
    query: useCallback((datasource) => datasource.api.webhooks.getAll(), []),
    initial: [],
  });

  /**
   * On Add Webhook
   */
  const onAddWebhook = useCallback(
    async ({ useFor, useForByDefault, ...webhook }: Webhook) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        await datasource.api.webhooks.create(webhook);
        await updateWebhooks();

        setAdding(false);
        notifySuccess([
          t('alertingPanel.panel.webhooksList.webhookCreatedTitle'),
          t('alertingPanel.panel.webhooksList.webhookCreatedMessage', { name: webhook.title }),
        ]);
      } catch {}
    },
    [datasource, t, updateWebhooks]
  );

  /**
   * On Update Webhook
   */
  const onUpdateWebhook = useCallback(
    async ({ useFor, useForByDefault, ...webhook }: Webhook) => {
      if (!datasource || !datasource.api || !editWebhook) {
        return;
      }

      try {
        await datasource.api.webhooks.update(editWebhook.id, webhook);
        await updateWebhooks();

        setEditWebhook(null);
        notifySuccess([
          t('alertingPanel.panel.webhooksList.webhookUpdatedTitle'),
          t('alertingPanel.panel.webhooksList.webhookUpdatedMessage', { name: editWebhook.title }),
        ]);
      } catch {}
    },
    [datasource, editWebhook, t, updateWebhooks]
  );

  /**
   * On Delete Webhook
   */
  const onDeleteWebhook = useCallback(
    async (webhook: Webhook) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        setDeleting(true);
        await datasource.api.webhooks.delete(webhook.id);
        await updateWebhooks();

        setDeleteWebhook(null);
        setDeleting(false);
        notifySuccess([
          t('alertingPanel.panel.webhooksList.webhookDeletedTitle'),
          t('alertingPanel.panel.webhooksList.webhookDeletedMessage', { name: webhook.title }),
        ]);
      } catch {
        setDeleting(false);
      }
    },
    [datasource, t, updateWebhooks]
  );

  /**
   * On Test Webhook
   */
  const onTestWebhook = useCallback(
    async ({ useFor, useForByDefault, ...webhook }: Webhook) => {
      if (!datasource || !datasource.api) {
        return;
      }

      const data = await datasource.api.webhooks.test(webhook);

      if (data.ok) {
        notifySuccess([
          t('alertingPanel.panel.webhooksList.webhookTestedTitle'),
          t('alertingPanel.panel.webhooksList.webhookTestedMessage', { name: webhook.title }),
        ]);
      } else {
        notifyError([t('api.error'), data.message]);
      }
    },
    [datasource, t]
  );

  /**
   * Refresh on dashboard update
   */
  useEffect(() => {
    /**
     * On Refresh
     */
    const subscriber = eventBus.getStream(RefreshEvent).subscribe(() => {
      updateWebhooks();
    });

    return () => {
      subscriber.unsubscribe();
    };
  }, [eventBus, updateWebhooks]);

  /**
   * Loading
   */
  if (loading) {
    return (
      <div>
        <Alert severity="info" title={t('basic.wait')} data-testid={TEST_IDS.panel.webhooksLoadingMessage}>
          <LoadingPlaceholder text={t('messages.loading')} />
        </Alert>
      </div>
    );
  }

  /**
   * Error
   */
  if (error) {
    return (
      <div>
        <Alert severity="error" title={t('messages.notAvailableTitle')}>
          {t('messages.notAvailableMessage')}
        </Alert>
      </div>
    );
  }

  return (
    <div className={styles.root}>
      {permissions.webhooks.add && (
        <div className={styles.header}>
          <Button icon="plus-circle" onClick={() => setAdding(true)}>
            {t('alertingPanel.panel.webhooksList.addButton')}
          </Button>
        </div>
      )}

      {webhooks.length === 0 ? (
        <Alert title={t('alertingPanel.panel.webhooksList.noItemsTitle')} severity="info">
          {t('alertingPanel.panel.webhooksList.noItemsMessage')}
        </Alert>
      ) : (
        webhooks.map((webhook: Webhook) => {
          const tags = [t(`alertingPanel.panel.webhookType.${webhook.type}`)];

          if (webhook.isDefault) {
            tags.push(t(`alertingPanel.panel.webhooksList.isDefault`));
          }

          return (
            <Card
              key={webhook.id}
              onClick={() => {
                if (permissions.webhooks.edit) {
                  setEditWebhook(webhook);
                }
              }}
            >
              <Card.Heading>{webhook.title}</Card.Heading>
              <Card.Meta>{webhook.type === WebhookType.REQUEST && webhook.options?.url}</Card.Meta>
              <Card.Tags>
                <TagList tags={tags} />
              </Card.Tags>
              <Card.Figure>
                <WebhookIcon size={40} fill={theme.colors.primary.main} fill2={theme.colors.border.medium} />
              </Card.Figure>
              <Card.SecondaryActions className={styles.secondaryActions}>
                {permissions.webhooks.test && (
                  <IconButton
                    name="heart-rate"
                    aria-label=""
                    tooltip={t('alertingPanel.panel.webhooksList.testButton')}
                    onClick={() => onTestWebhook(webhook)}
                  />
                )}
                {permissions.webhooks.edit && (
                  <IconButton
                    name="edit"
                    aria-label=""
                    tooltip={t('alertingPanel.panel.webhooksList.editButton')}
                    onClick={() => {
                      setEditWebhook(webhook);
                    }}
                  />
                )}
                {permissions.webhooks.delete && (
                  <IconButton
                    name="trash-alt"
                    aria-label=""
                    tooltip={
                      !!webhook.useFor?.length
                        ? t('alertingPanel.panel.webhooksList.deleteButtonDisabled')
                        : t('alertingPanel.panel.webhooksList.deleteButton')
                    }
                    onClick={() => setDeleteWebhook(webhook)}
                    disabled={!!webhook.useFor?.length}
                  />
                )}
              </Card.SecondaryActions>
            </Card>
          );
        })
      )}

      {isAdding && (
        <Drawer
          title={t('alertingPanel.panel.webhooksList.addDrawer.title')}
          onClose={() => setAdding(false)}
          scrollableContent
        >
          <WebhookDetails datasource={datasource} onClose={() => setAdding(false)} onSave={onAddWebhook} />
        </Drawer>
      )}

      {!!editWebhook && (
        <Drawer
          title={t('alertingPanel.panel.webhooksList.editDrawer.title')}
          onClose={() => setEditWebhook(null)}
          scrollableContent
        >
          <WebhookDetails
            datasource={datasource}
            onClose={() => setEditWebhook(null)}
            onSave={onUpdateWebhook}
            initial={editWebhook}
          />
        </Drawer>
      )}
      {!!deleteWebhook && (
        <ConfirmModal
          isOpen={true}
          title={t('alertingPanel.panel.webhooksList.deleteConfirmModal.title')}
          body={t('alertingPanel.panel.webhooksList.deleteConfirmModal.body', { name: deleteWebhook.title })}
          confirmText={
            deleting
              ? t('alertingPanel.panel.webhooksList.deleteConfirmModal.confirmButtonLoading')
              : t('alertingPanel.panel.webhooksList.deleteConfirmModal.confirmButton')
          }
          onDismiss={() => setDeleteWebhook(null)}
          onConfirm={() => onDeleteWebhook(deleteWebhook)}
        />
      )}
    </div>
  );
};
