import {
  Alert as AlertData,
  AlertState,
  AlertTimeRangeSource,
  DataSourceApi,
  EvaluationType,
  ThresholdColors,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import {
  Alert,
  Button,
  FieldSet,
  InlineField,
  InlineFieldRow,
  Input,
  Label,
  LoadingPlaceholder,
  RadioButtonGroup,
  Select,
  useStyles2,
} from '@grafana/ui';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import {
  ALERT_TIME_RANGE_SOURCE_OPTIONS,
  ANNOTATION_OPTIONS,
  EVALUATION_TYPE_OPTIONS,
  THRESHOLD_COLORS_OPTIONS,
} from '../../constants';
import { isEvaluationType, secondsToRelativeFormat } from '../../utils';
import { getStyles } from './AlertOverview.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * Alert
   *
   * @type {AlertData}
   */
  alert: AlertData;
}

/**
 * Alert Details
 */
export const AlertOverview: React.FC<Props> = ({ datasource, onClose, alert }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t } = useTranslation();

  /**
   * Label Width
   */
  const labelWidth = 18;

  /**
   * Dashboard Info
   */
  const { data: dashboardInfo } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (alert.dashboardId) {
          return datasource.api.grafana.getDashboardInfo(alert.dashboardId);
        }
        return null;
      },
      [alert.dashboardId]
    ),
    datasource,
  });

  /**
   * Webhooks
   */
  const { data: webhooks, loading: isWebhooksLoading } = useData({
    initial: [],
    query: useCallback(async (datasource) => {
      return datasource.api.webhooks.getAll();
    }, []),
    datasource,
  });

  /**
   * Trigger Event
   */
  const { data: triggerEvent, loading: isTriggerEventLoading } = useData<unknown>({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (alert.state === AlertState.ERROR) {
          return datasource.api.alerts.getTriggerEvent(alert.id);
        }
        return null;
      },
      [alert.id, alert.state]
    ),
    datasource,
  });

  /**
   * Default Webhook
   */
  const { data: defaultWebhook, loading: isDefaultWebhookLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (!alert.webhookIds.length) {
          return datasource.api.webhooks.getDefault();
        }
        return null;
      },
      [alert.webhookIds.length]
    ),
    datasource,
  });

  /**
   * Panel
   */
  const panelTitle = useMemo(() => {
    if (!dashboardInfo) {
      return '';
    }

    const panel = dashboardInfo.panels.find((panel) => panel.id === alert.panelId);

    if (!panel) {
      return t('alertingPanel.panel.alertOverview.removedPanel');
    }

    return `${panel?.title || t('alertingPanel.panel.alertOverview.untitled')} [${panel?.type}]`;
  }, [alert.panelId, dashboardInfo, t]);

  /**
   * Web Hook Options
   */
  const webHookOptions = useMemo(
    () =>
      webhooks.map((webhook) => ({
        value: webhook.id,
        label: webhook.title,
      })),
    [webhooks]
  );

  /**
   * Annotation Options
   */
  const annotationOptions = useMemo(() => {
    return ANNOTATION_OPTIONS(t);
  }, [t]);

  /**
   * Time Range Source Options
   */
  const timeRangeSourceOptions = useMemo(() => {
    return ALERT_TIME_RANGE_SOURCE_OPTIONS(t);
  }, [t]);

  /**
   * Evaluation Type Options
   */
  const evaluationTypeOptions = useMemo(() => {
    return EVALUATION_TYPE_OPTIONS(t);
  }, [t]);

  /**
   * Threshold Color Options
   */
  const thresholdColorsOptions = useMemo(() => {
    return THRESHOLD_COLORS_OPTIONS(t);
  }, [t]);

  return (
    <>
      <InlineField
        label={t('alertingPanel.panel.alertOverview.targetLabel')}
        className={styles.target}
        labelWidth={labelWidth}
      >
        <Label category={[dashboardInfo?.title ?? '']} className={styles.targetLabel}>
          {panelTitle}
        </Label>
      </InlineField>

      <FieldSet label={t('alertingPanel.panel.alertOverview.timeRangeLabel')}>
        <InlineField
          label={t('alertingPanel.panel.alertOverview.timeRangeSourceLabel')}
          labelWidth={labelWidth}
          disabled={true}
        >
          <RadioButtonGroup options={timeRangeSourceOptions} value={alert.timeRangeSource} />
        </InlineField>

        {alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED && !!alert.timeRange && (
          <InlineFieldRow>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.from')}
              disabled={true}
              labelWidth={labelWidth}
            >
              <Input value={secondsToRelativeFormat(alert.timeRange.from)}></Input>
            </InlineField>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.to')}
              disabled={true}
              labelWidth={labelWidth}
              grow
            >
              <Input value={secondsToRelativeFormat(alert.timeRange.to)}></Input>
            </InlineField>
          </InlineFieldRow>
        )}

        {alert.timeRangeSource === AlertTimeRangeSource.DASHBOARD && dashboardInfo?.time && (
          <InlineFieldRow>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.from')}
              disabled={true}
              labelWidth={labelWidth}
            >
              <Input value={dashboardInfo?.time.from}></Input>
            </InlineField>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.timeRange.to')}
              disabled={true}
              labelWidth={labelWidth}
              grow
            >
              <Input value={dashboardInfo?.time.to}></Input>
            </InlineField>
          </InlineFieldRow>
        )}
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.alertOverview.evaluationLabel')}>
        <InlineField
          label={t('alertingPanel.panel.alertOverview.evaluationTypeLabel')}
          labelWidth={labelWidth}
          disabled={true}
        >
          <RadioButtonGroup options={evaluationTypeOptions} value={alert.evaluationType || EvaluationType.THRESHOLDS} />
        </InlineField>

        {isEvaluationType(alert, EvaluationType.PATTERN) && (
          <>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.evaluation.fieldLabel')}
              labelWidth={labelWidth}
              grow={true}
              disabled={true}
            >
              <Input value={alert.evaluation.field} />
            </InlineField>
            <InlineField
              label={t('alertingPanel.panel.alertOverview.evaluation.patternLabel')}
              labelWidth={labelWidth}
              grow={true}
              disabled={true}
            >
              <Input value={alert.evaluation.pattern} />
            </InlineField>
          </>
        )}

        {isEvaluationType(alert, EvaluationType.THRESHOLDS) && (
          <InlineField
            label={t('alertingPanel.panel.editAlert.thresholdColors.fieldLabel')}
            labelWidth={labelWidth}
            shrink={true}
            grow={true}
            disabled={true}
          >
            <RadioButtonGroup options={thresholdColorsOptions} value={ThresholdColors.GREEN} />
          </InlineField>
        )}
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.alertOverview.actionLabel')}>
        <InlineFieldRow>
          <InlineField
            label={t('alertingPanel.panel.alertOverview.webhooksLabel')}
            labelWidth={labelWidth}
            grow={true}
            disabled={true}
          >
            <Select
              onChange={() => {}}
              value={alert.webhookIds.length ? alert.webhookIds : defaultWebhook ? [defaultWebhook.id] : []}
              options={webHookOptions}
              isMulti={true}
              isLoading={isWebhooksLoading || isDefaultWebhookLoading}
            />
          </InlineField>
        </InlineFieldRow>
        <InlineFieldRow>
          <InlineField
            label={t('alertingPanel.panel.alertOverview.annotationsLabel')}
            grow={true}
            disabled={true}
            labelWidth={labelWidth}
          >
            <RadioButtonGroup options={annotationOptions} value={alert.annotations} />
          </InlineField>
        </InlineFieldRow>
      </FieldSet>

      {alert.state === AlertState.ERROR && (
        <FieldSet label={t('alertingPanel.panel.alertOverview.errorDetailsLabel')}>
          {isTriggerEventLoading ? (
            <Alert severity="info" title={t('basic.wait')}>
              <LoadingPlaceholder text={t('messages.loading')} />
            </Alert>
          ) : (
            <code className={styles.triggerEventCode}>{JSON.stringify(triggerEvent, null, 2)}</code>
          )}
        </FieldSet>
      )}

      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose}>
          {t('alertingPanel.panel.alertOverview.closeButton')}
        </Button>
      </footer>
    </>
  );
};
