/**
 * Alert Status
 */
export enum AlertStatus {
  ACTIVE = 'active',
  PAUSED = 'paused',
}

/**
 * Alert Time Range Source
 */
export enum AlertTimeRangeSource {
  CONFIGURED = 'configured',
  DASHBOARD = 'dashboard',
}

/**
 * Evaluation Type
 */
export enum EvaluationType {
  ANOMALY = 'anomaly',
  PATTERN = 'pattern',
  THRESHOLDS = 'thresholds',
}

/**
 * Threshold Colors
 */
export enum ThresholdColors {
  GREEN = 'green',
}

/**
 * Alert State
 */
export enum AlertState {
  SCHEDULED = 'scheduled',
  OK = 'ok',
  ALERTING = 'alerting',
  ERROR = 'error',
}

/**
 * Alert Time Range
 */
export interface AlertTimeRange {
  /**
   * From
   *
   * @type {number}
   */
  from: number;

  /**
   * To
   *
   * @type {number}
   */
  to: number;
}

/**
 * Base Alert
 */
interface BaseAlert {
  /**
   * Id
   *
   * @type {string}
   */
  id: string;

  /**
   * Title
   *
   * @type {string}
   */
  title: string;

  /**
   * Dashboard ID
   *
   * @type {string}
   */
  dashboardId: string;

  /**
   * Panel ID
   *
   * @type {number}
   */
  panelId: number;

  /**
   * Schedule
   *
   * @type {string}
   */
  schedule: string;

  /**
   * Webhook Ids
   *
   * @type {string[]}
   */
  webhookIds: string[];

  /**
   * Annotations
   *
   * @type {boolean}
   */
  annotations?: boolean;

  /**
   * Status
   *
   * @type {AlertStatus}
   */
  status: AlertStatus;

  /**
   * Time Range Source
   *
   * @type {AlertTimeRangeSource}
   */
  timeRangeSource: AlertTimeRangeSource;

  /**
   * Time Range
   *
   * @type {AlertTimeRange}
   */
  timeRange?: AlertTimeRange;

  /**
   * State
   *
   * @type {AlertState}
   */
  state: AlertState;

  /**
   * Last Triggered
   *
   * @type {string | null}
   */
  lastTriggered: string | null;
}

/**
 * Alert
 */
export type Alert = BaseAlert &
  (
    | { evaluationType: EvaluationType.THRESHOLDS | EvaluationType.ANOMALY; evaluation: object }
    | { evaluationType: EvaluationType.PATTERN; evaluation: AlertEvaluationPattern }
  );

/**
 * Alert Evaluation Pattern
 */
interface AlertEvaluationPattern {
  /**
   * Field
   *
   * @type {string}
   */
  field: string;

  /**
   * Pattern
   *
   * @type {string}
   */
  pattern: string;
}

/**
 * Alert Evaluation
 */
export type AlertEvaluation = AlertEvaluationPattern;

/**
 * Edit Alert Data
 */
export interface EditAlertData extends Omit<Alert, 'dashboardId' | 'panelId' | 'state' | 'lastTriggered'> {
  dashboardId: Nullable<Alert['dashboardId']>;
  panelId: Nullable<Alert['panelId']>;
}

/**
 * Alert With Info
 */
export type AlertWithInfo = Alert & {
  /**
   * Dashboard Title
   *
   * @type {string | null}
   */
  dashboardTitle: string | null;

  /**
   * Panel Title
   *
   * @type {string | null}
   */
  panelTitle: string | null;

  /**
   * Is Target Exists
   *
   * @type {boolean}
   */
  isTargetExists: boolean;
};
