import {
  Alert,
  AlertStatus,
  AlertTimeRangeSource,
  DataSourceApi,
  EditAlertData,
  EvaluationType,
  ThresholdColors,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import { cx } from '@emotion/css';
import {
  Button,
  FieldSet,
  InlineField,
  InlineFieldRow,
  Input,
  RadioButtonGroup,
  RelativeTimeRangePicker,
  Select,
  useStyles2,
} from '@grafana/ui';
import cronstrue from 'cronstrue';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import {
  ALERT_TIME_RANGE_SOURCE_OPTIONS,
  ANNOTATION_OPTIONS,
  EVALUATION_TYPE_OPTIONS,
  THRESHOLD_COLORS_OPTIONS,
} from '../../constants';
import { isEvaluationType } from '../../utils';
import { getStyles } from './EditAlert.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * On Save
   */
  onSave: (data: EditAlertData) => void;

  /**
   * Initial
   */
  initial?: EditAlertData;
}

/**
 * Alert Details
 */
export const EditAlert: React.FC<Props> = ({ datasource, onSave, onClose, initial }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t, i18n } = useTranslation();

  /**
   * Label Width
   */
  const labelWidth = 18;

  /**
   * Alert
   */
  const [alert, setAlert] = useState<EditAlertData>(
    initial || {
      annotations: false,
      dashboardId: null,
      evaluationType: EvaluationType.THRESHOLDS,
      id: '',
      panelId: null,
      schedule: '*/5 * * * *',
      status: AlertStatus.ACTIVE,
      timeRangeSource: AlertTimeRangeSource.DASHBOARD,
      title: '',
      webhookIds: [],
      evaluation: {},
    }
  );

  /**
   * Saving
   */
  const [saving, setSaving] = useState(false);

  /**
   * Dashboards
   */
  const { data: dashboards, loading: isDashboardLoading } = useData({
    initial: [],
    query: useCallback((datasource) => datasource.api.grafana.getDashboards(), []),
    datasource,
  });

  /**
   * Dashboard Options
   */
  const dashboardOptions = useMemo(() => {
    return dashboards.map((dashboard) => ({
      value: dashboard.uid,
      label: dashboard.title,
    }));
  }, [dashboards]);

  /**
   * Dashboard Info
   */
  const { data: dashboardInfo, loading: isDashboardInfoLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (alert.dashboardId) {
          return datasource.api.grafana.getDashboardInfo(alert.dashboardId);
        }
        return null;
      },
      [alert.dashboardId]
    ),
    datasource,
  });

  /**
   * Webhooks
   */
  const { data: webhooks, loading: isWebhooksLoading } = useData({
    initial: [],
    query: useCallback(async (datasource) => {
      return datasource.api.webhooks.getAll();
    }, []),
    datasource,
  });

  /**
   * Default Webhook
   */
  const { data: defaultWebhook, loading: isDefaultWebhookLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (!alert.webhookIds.length) {
          return datasource.api.webhooks.getDefault();
        }
        return null;
      },
      [alert.webhookIds.length]
    ),
    datasource,
  });

  /**
   * Panel Options
   */
  const panelOptions = useMemo(() => {
    if (!dashboardInfo) {
      return [];
    }

    return dashboardInfo.panels.map((panel) => ({
      value: panel.id,
      label: `${panel.title || t('alertingPanel.panel.editAlert.panelOptions.untitled')} [${panel.type}]`,
    }));
  }, [dashboardInfo, t]);

  /**
   * Web Hook Options
   */
  const webHookOptions = useMemo(
    () =>
      webhooks.map((webhook) => ({
        value: webhook.id,
        label: webhook.title,
      })),
    [webhooks]
  );

  /**
   * Annotation Options
   */
  const annotationOptions = useMemo(() => {
    return ANNOTATION_OPTIONS(t);
  }, [t]);

  /**
   * Schedule Description
   */
  const scheduleDescription = useMemo(() => {
    try {
      return cronstrue.toString(alert.schedule, { locale: i18n.language });
    } catch {
      return '';
    }
  }, [alert.schedule, i18n.language]);

  /**
   * Is Valid
   */
  const isValid = useMemo(() => {
    if (!alert.title || !alert.panelId || !alert.schedule || !scheduleDescription) {
      return false;
    }

    if (alert.webhookIds.length === 0 && !defaultWebhook) {
      return false;
    }

    if (isEvaluationType(alert, EvaluationType.PATTERN) && !alert.evaluation.pattern.trim()) {
      return false;
    }

    return true;
  }, [alert, defaultWebhook, scheduleDescription]);

  /**
   * Time Range Source Options
   */
  const timeRangeSourceOptions = useMemo(() => {
    return ALERT_TIME_RANGE_SOURCE_OPTIONS(t);
  }, [t]);

  /**
   * Evaluation Type Options
   */
  const evaluationTypeOptions = useMemo(() => {
    return EVALUATION_TYPE_OPTIONS(t);
  }, [t]);

  /**
   * Threshold Color Options
   */
  const thresholdColorsOptions = useMemo(() => {
    return THRESHOLD_COLORS_OPTIONS(t);
  }, [t]);

  return (
    <>
      <FieldSet>
        <InlineField
          label={t('alertingPanel.panel.editAlert.titleLabel')}
          grow={true}
          labelWidth={labelWidth}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAlert({
                ...alert,
                title: event.currentTarget.value,
              });
            }}
            value={alert.title}
          />
        </InlineField>

        <InlineField
          label={t('alertingPanel.panel.editAlert.scheduleLabel')}
          labelWidth={labelWidth}
          grow={true}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAlert({
                ...alert,
                schedule: event.currentTarget.value,
              });
            }}
            value={alert.schedule}
            placeholder={t('alertingPanel.panel.editAlert.schedulePlaceholder')}
          />
        </InlineField>
        <InlineField label={''} labelWidth={labelWidth} transparent={true} grow={true} shrink={true}>
          <div
            className={cx(styles.helpText, {
              [styles.helpTextInvalid]: !scheduleDescription,
            })}
          >
            {scheduleDescription || t('alertingPanel.panel.editAlert.invalidSchedule')}
          </div>
        </InlineField>
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.targetLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.dashboardLabel')}
          grow={true}
          labelWidth={labelWidth}
          required={true}
        >
          <Select
            onChange={(event) => {
              setAlert({
                ...alert,
                dashboardId: event.value!,
                panelId: null,
              });
            }}
            value={alert.dashboardId}
            options={dashboardOptions}
            isLoading={isDashboardLoading}
          />
        </InlineField>

        <InlineField
          label={t('alertingPanel.panel.editAlert.panelLabel')}
          grow={true}
          labelWidth={labelWidth}
          required={true}
        >
          <Select
            onChange={(event) => {
              setAlert({
                ...alert,
                panelId: event.value!,
              });
            }}
            value={alert.panelId}
            options={panelOptions}
            isLoading={isDashboardInfoLoading}
          />
        </InlineField>
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.timeRangeLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.timeRangeSourceLabel')}
          labelWidth={labelWidth}
          required={true}
        >
          <RadioButtonGroup
            options={timeRangeSourceOptions}
            onChange={(value) => {
              setAlert({
                ...alert,
                timeRangeSource: value,
                timeRange:
                  value === AlertTimeRangeSource.DASHBOARD
                    ? undefined
                    : {
                        from: 600,
                        to: 0,
                      },
              });
            }}
            value={alert.timeRangeSource}
          />
        </InlineField>

        {alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED && alert.timeRange && (
          <InlineField
            label={t('alertingPanel.panel.editAlert.timeRangeLabel')}
            labelWidth={labelWidth}
            required={true}
          >
            <RelativeTimeRangePicker
              timeRange={alert.timeRange}
              onChange={(event) => {
                setAlert({
                  ...alert,
                  timeRange: event,
                });
              }}
            />
          </InlineField>
        )}

        {alert.timeRangeSource === AlertTimeRangeSource.DASHBOARD && dashboardInfo?.time && (
          <InlineFieldRow>
            <InlineField label={t('alertingPanel.panel.editAlert.timeRange.from')} labelWidth={labelWidth} disabled>
              <Input value={dashboardInfo?.time.from}></Input>
            </InlineField>
            <InlineField label={t('alertingPanel.panel.editAlert.timeRange.to')} labelWidth={labelWidth} grow disabled>
              <Input value={dashboardInfo?.time.to}></Input>
            </InlineField>
          </InlineFieldRow>
        )}
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.evaluationLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.evaluationTypeLabel')}
          labelWidth={labelWidth}
          required={true}
        >
          <RadioButtonGroup
            options={evaluationTypeOptions}
            onChange={(value) => {
              setAlert({
                ...alert,
                evaluationType: value,
                evaluation:
                  value === EvaluationType.PATTERN
                    ? {
                        field: '',
                        pattern: '',
                      }
                    : {},
              });
            }}
            value={alert.evaluationType || EvaluationType.THRESHOLDS}
          />
        </InlineField>
        {isEvaluationType(alert, EvaluationType.PATTERN) && (
          <>
            <InlineField
              label={t('alertingPanel.panel.editAlert.evaluation.fieldLabel')}
              labelWidth={labelWidth}
              tooltip={t('alertingPanel.panel.editAlert.evaluation.fieldTooltip')}
              grow={true}
            >
              <Input
                onChange={(event) => {
                  setAlert({
                    ...alert,
                    evaluation: {
                      ...alert.evaluation,
                      field: event.currentTarget.value,
                    },
                  });
                }}
                value={alert.evaluation.field}
              />
            </InlineField>
            <InlineField
              label={t('alertingPanel.panel.editAlert.evaluation.patternLabel')}
              labelWidth={labelWidth}
              required={true}
              tooltip={t('alertingPanel.panel.editAlert.evaluation.patternTooltip')}
              grow={true}
            >
              <Input
                onChange={(event) => {
                  setAlert({
                    ...alert,
                    evaluation: {
                      ...alert.evaluation,
                      pattern: event.currentTarget.value,
                    },
                  });
                }}
                value={alert.evaluation.pattern}
              />
            </InlineField>
          </>
        )}

        {isEvaluationType(alert, EvaluationType.THRESHOLDS) && (
          <InlineField
            label={t('alertingPanel.panel.editAlert.thresholdColors.fieldLabel')}
            labelWidth={labelWidth}
            shrink={true}
            grow={true}
            tooltip={t('alertingPanel.panel.editAlert.thresholdColors.fieldTooltip')}
          >
            <RadioButtonGroup options={thresholdColorsOptions} value={ThresholdColors.GREEN} />
          </InlineField>
        )}
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.actionLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.webhooksLabel')}
          labelWidth={labelWidth}
          grow={true}
          invalid={alert.webhookIds.length > 0 ? false : !defaultWebhook}
          error={t('alertingPanel.panel.editAlert.noWebhooksError')}
        >
          <Select
            onChange={(event) => {
              const options = Array.isArray(event) ? event : [event!];
              setAlert({
                ...alert,
                webhookIds: options.map(({ value }) => value).filter((value) => !!value),
              });
            }}
            value={alert.webhookIds}
            options={webHookOptions}
            isMulti={true}
            isLoading={isWebhooksLoading || isDefaultWebhookLoading}
            placeholder={
              defaultWebhook
                ? t('alertingPanel.panel.editAlert.webhooksPlaceholder', { name: defaultWebhook.title })
                : t('alertingPanel.panel.editAlert.webhooksNoDefaultMessage')
            }
          />
        </InlineField>
        <InlineField
          label={t('alertingPanel.panel.editAlert.annotationsLabel')}
          labelWidth={labelWidth}
          grow={true}
          required={true}
        >
          <RadioButtonGroup
            options={annotationOptions}
            onChange={(value) => {
              setAlert((alert) => ({
                ...alert,
                annotations: value,
              }));
            }}
            value={alert.annotations}
          />
        </InlineField>
      </FieldSet>

      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose}>
          {t('alertingPanel.panel.editAlert.closeButton')}
        </Button>
        <Button
          onClick={async () => {
            setSaving(true);
            await onSave(alert as Alert);
            setSaving(false);
          }}
          disabled={!isValid || saving}
        >
          {t('alertingPanel.panel.editAlert.saveButton')}
        </Button>
      </footer>
    </>
  );
};
