import { Alert, AlertWithInfo, EditAlertData, EvaluationType } from '@bi/volkovlabs-engine-datasource';

/**
 * Convert To Edit Alert
 * Pass Particular fields to prevent type inconsistent which typescript can't verify
 */
export const convertToEditAlert = (alertWithInfo: AlertWithInfo): EditAlertData => {
  return {
    annotations: alertWithInfo.annotations,
    dashboardId: alertWithInfo.dashboardId,
    evaluationType: alertWithInfo.evaluationType,
    id: alertWithInfo.id,
    panelId: alertWithInfo.panelId,
    schedule: alertWithInfo.schedule,
    status: alertWithInfo.status,
    timeRange: alertWithInfo.timeRange,
    timeRangeSource: alertWithInfo.timeRangeSource,
    title: alertWithInfo.title,
    webhookIds: alertWithInfo.webhookIds,
    evaluation: alertWithInfo.evaluation,
  };
};

/**
 * Units
 */
const units: Record<string, number> = {
  w: 604800,
  d: 86400,
  h: 3600,
  m: 60,
  s: 1,
};

/**
 * Seconds To Relative Format
 * @param seconds
 */
export const secondsToRelativeFormat = (seconds: number): string => {
  if (seconds <= 0) {
    return 'now';
  }

  if (seconds >= units.w && seconds % units.w === 0) {
    return `now-${seconds / units.w}w`;
  }

  if (seconds >= units.d && seconds % units.d === 0) {
    return `now-${seconds / units.d}d`;
  }

  if (seconds >= units.h && seconds % units.h === 0) {
    return `now-${seconds / units.h}h`;
  }

  if (seconds >= units.m && seconds % units.m === 0) {
    return `now-${seconds / units.m}m`;
  }

  return `now-${seconds}s`;
};

/**
 * Is Evaluation Type
 */
export const isEvaluationType = <T extends EvaluationType>(
  alert: Alert | EditAlertData,
  evaluationType: T
): alert is Alert & { evaluationType: T } => {
  return alert.evaluationType === evaluationType;
};
